      const channelIcon = {
        [CHATROOM_PRIVACY_LEVEL.PRIVATE]: 'lock',
        [CHATROOM_PRIVACY_LEVEL.GROUP]: 'hashtag',
        [CHATROOM_PRIVACY_LEVEL.PUBLIC]: 'unlock-alt'
