        action: 'markAsUnread',
        icon: 'envelope',
        conditionToShow: true
      }, {
        name: L('Edit'),
        action: 'editMessage',
        icon: 'pencil-alt',
        conditionToShow: !this.isDesktopScreen && this.isEditable
      }, {
        name: L('Reply'),
        action: 'reply',
        icon: 'reply',
        conditionToShow: !this.isDesktopScreen && this.isReplyable
