import sbp from '@sbp/sbp'
import Tooltip from '../../../../../frontend/views/components/Tooltip.vue'
import { MESSAGE_VARIANTS } from '../../../../../frontend/model/contracts/shared/constants.js'
import { getFileExtension, getFileType, formatBytesDecimal } from '../../../../../frontend/views/utils/filters.js'
import { Secret } from '../../../../../shared/domains/chelonia/Secret.js'
import { OPEN_MODAL, DELETE_ATTACHMENT } from '../../../../../frontend/utils/events.js'
import { L } from '../../../../../frontend/common/common.js'
