import sbp from '@sbp/sbp'
import MessageBase from './MessageBase.vue'
import { MESSAGE_VARIANTS } from '../../../../frontend/model/contracts/shared/constants.js'
import { DAYS_MILLIS, MINS_MILLIS } from '../../../../frontend/model/contracts/shared/time.js'
